@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900&display=swap);
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: 'Poppins', sans-serif;
}

:root {
	--primary-color: #080808;
	--secondary-color: #fc4444;
	--accent-color: #c7eeff;
	--hover-color: #292929;
	--logo-bg-color: #a2bfbd;
	--bg-color: #453b36;
	--text-color: #fc4444;
}

body {
	background: #453b36;
	background: var(--bg-color);
}

.container-main {
	/*border: 5px red solid;*/
	display: flex;
	background: black;
}

/*Anschrift+Kontakt-Container*/
.container-1 {
	/* border: 5px #ccc solid;*/
	padding: 5px 20px;
	width: 60%;
	font-style: 'Poppins', sans-serif;
	color: var(--secondary-color);
	display: flex;
	justify-content: space-between;
	grid-gap: 20px;
	gap: 20px;
	z-index: 1000;
}

/*Copyright-Container*/
.container-2 {
	width: 100%;
	font-style: 'Poppins', sans-serif;
	color: var(--secondary-color);
	margin: auto;
}

/*Logos Container*/
.container-3 {
	/*border: 5px yellow solid;*/
	padding: 5px 20px;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	width: 40%;
	grid-gap: 10px;
	gap: 10px;
	z-index: 1000;
}

/*Kontakt*/
.box-1 {
	/*background: dimgray;*/
	list-style-type: none;
	font-size: 13px;
}

/*Anschrift*/
.box-2 {
	/* background: darkslateblue;*/
	list-style-type: none;
	font-size: 13px;
}

/*Copyright zeug ganz unten*/
.box-3 {
	border-top: 1px solid grey;
	background: black;
	font-size: 11px;
	list-style-type: none;
	text-align: center;
	padding: 3px;
}

/*Logos*/
/*.box-4 {
    /*background: black;}*/

a {
	color: var(--secondary-color);
}

@media screen and (max-width: 991px) {
	.container-1 {
		flex-direction: column;
		width: 50%;
	}
	.container-3 {
		width: 50%;
		margin: auto;
	}
}

